import { _decorator, Component, v3, Vec2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Missile')
export class Missile extends Component {
    /**目标地点 */
    public targetPos: Vec2 = new Vec2();
    /**角速度 */
    public angleSpeed: number = 5;
    /**移动速度 */
    public moveSpeed: number = 5;

    /**移动限制距离，相距目的地距离>moveLimit时才会移动，防止抖动 */
    private moveLimit: number = 20;
    /**每帧变化的角度 */
    private stepAngle: number = null;

    public setTargetPos(xPos: number, yPos: number) {
        this.targetPos.x = xPos;
        this.targetPos.y = yPos;
        this.stepAngle = null;
    }

    /**重置 */
    public reset() {
        this.stepAngle = null;
    }
    start() {

    }

    update(deltaTime: number) {
        this.rotation();
    }
    //旋转
    rotation() {
        if (Math.sqrt(Math.pow(this.node.position.x - this.node.parent.getChildByName("enemy").position.x, 2) +
            Math.pow(this.node.position.y - this.node.parent.getChildByName("enemy").position.y, 2)) > this.moveLimit) {
            let theangle = Math.atan2(this.node.position.y - this.node.parent.getChildByName("enemy").position.y,
                this.node.position.x - this.node.parent.getChildByName("enemy").position.x);
            let theta = theangle * 180 / Math.PI;
            let curAngle = this.node.angle;
            let distAngle = theta - curAngle;
            this.node.eulerAngles = v3(0, 0, theta);
            if (this.stepAngle == null) {
                this.stepAngle = distAngle > 0 ? this.angleSpeed : -this.angleSpeed;
            }
            if (Math.abs((curAngle + 360) % 360 - (theta + 360) % 360) > (Math.abs(this.stepAngle) + 1)) {
                //角度未达到临近值才会++，防止抖动
                if (Math.abs(this.node.angle - theta) > (Math.abs(this.stepAngle) + 1)) {
                    this.node.angle += this.stepAngle;
                }
            }
            let nextRadian = this.node.angle * Math.PI / 180;
            let nodeX = this.node.position.x;
            let nodeY = this.node.position.y;
            nodeX += Math.cos(nextRadian) * this.moveSpeed;
            nodeY += Math.sin(nextRadian) * this.moveSpeed;
            this.node.setPosition(nodeX, nodeY);
        }
    }
    die() {
        this.node.destroy();
    }
}

